const mutations = {
    sumLike(state) {
        state.likeNum--
    },
    addLike(state) {
        state.likeNum++
    },
    addComments(state) {
        state.commentsNum++
    },
    ADDESSAY(state, data) {
        state.recommendlist.unshift(data)
    }
};
const actions = {
    addEssay({ commit }, data = {}) {
        commit('ADDESSAY', data)
    }
};
const getters = {};
export default {
    state, mutations, actions, getters
}
