//axios
import axios from 'axios';

//1.axioscreateaxios
//2.requestaxios
const request = axios.create({
    //
    baseURL: "/mock",
    //
    timeout: 5000,
});


//
request.interceptors.request.use((config) => {
    //config:header
    return config;
});

//
request.interceptors.response.use((res) => {
    //
    return res.data;
}, (error) => {
    //
    return Promise.reject(new Error('faile'));
});

export default request;